.contact_form {
  width: 100%;
  max-width: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  margin: 0 auto;
  margin-top: 5rem;
  margin-bottom: 20rem;
  padding: 0 1rem;
}
.contact_form input, .contact_form textarea {
  box-shadow: 0.2rem 0.2rem #19252a, inset -0.2rem -0.2rem #19252a, -0.2rem -0.2rem #19252a, inset 0.2rem 0.2rem #19252a;
  padding: 1.5rem 1rem;
  width: 100%;
  font-size: 1.6rem;
  border: none;
  border-radius: 1rem;
  outline: none;
  background: rgba(0, 0, 0, 0.1);
  color: white;
  margin: 1rem 0;
}
.contact_form input:hover, .contact_form textarea:hover {
  transition: 0.6s;
}
.contact_form input::-moz-placeholder, .contact_form textarea::-moz-placeholder {
  color: white;
}
.contact_form input:-ms-input-placeholder, .contact_form textarea:-ms-input-placeholder {
  color: white;
}
.contact_form input::placeholder, .contact_form textarea::placeholder {
  color: white;
}
.contact_form #submit_button, .contact_form .submit_button {
  width: auto;
  padding: 1.5rem 2rem;
  cursor: pointer;
}
.contact_form #submit_button:hover, .contact_form .submit_button:hover {
  transform: scale(1.05);
  box-shadow: 0.2rem 0.2rem #19252a, inset 0.2rem 0.2rem #19252a, 0.2rem 0.2rem #19252a, inset 0.2rem 0.2rem #19252a;
}
.contact_form textarea {
  height: 15rem;
  resize: none;
}
.contact_form span a {
  color: white;
  border: none;
  border-bottom: 0.1rem solid rgba(255, 255, 255, 0.5098039216);
}/*# sourceMappingURL=Forget_Password.css.map */