body .footer {
  margin-top: auto;
  width: 100%;
  z-index: 1;
  --footer-background:#141414;
  display: grid;
  position: relative;
  grid-area: footer;
  min-height: 12rem;
}
body .footer .bubbles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1rem;
  background: var(--footer-background);
  filter: url("#blob");
}
body .footer .bubbles .bubble {
  position: absolute;
  left: var(--position, 50%);
  background: var(--footer-background);
  border-radius: 100%;
  -webkit-animation: bubble-size var(--time, 4s) ease-in infinite var(--delay, 0s), bubble-move var(--time, 4s) ease-in infinite var(--delay, 0s);
          animation: bubble-size var(--time, 4s) ease-in infinite var(--delay, 0s), bubble-move var(--time, 4s) ease-in infinite var(--delay, 0s);
  transform: translate(-50%, 100%);
}
body .footer .footer_content {
  z-index: 2;
  padding: 2rem;
  background: var(--footer-background);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
body .footer .footer_content h5 {
  color: #ffffff;
  text-shadow: 0.1rem 0.2rem 0.2rem #000000;
  border-bottom: 0.2rem solid rgba(255, 255, 255, 0.3803921569);
  padding-bottom: 0.6rem;
  padding-top: 5rem;
}
body .footer .footer_content span {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
body .footer .footer_content span a {
  border-right: 0.3rem solid rgba(255, 255, 255, 0.232);
  border-left: 0.3rem solid rgba(255, 255, 255, 0.232);
  padding: 0 1rem;
  cursor: pointer;
  margin: 1rem;
}
body .footer .footer_content span a:hover {
  transform: scale(1.1);
}
body .footer .footer_content span a i {
  margin-right: 0.5rem;
}
body .footer .footer_content h3 {
  font-size: 1.5rem;
  text-align: center;
}

@-webkit-keyframes bubble-size {
  0%, 75% {
    width: var(--size, 4rem);
    height: var(--size, 4rem);
  }
  100% {
    width: 0rem;
    height: 0rem;
  }
}

@keyframes bubble-size {
  0%, 75% {
    width: var(--size, 4rem);
    height: var(--size, 4rem);
  }
  100% {
    width: 0rem;
    height: 0rem;
  }
}
@-webkit-keyframes bubble-move {
  0% {
    bottom: -4rem;
  }
  100% {
    bottom: var(--distance, 10rem);
  }
}
@keyframes bubble-move {
  0% {
    bottom: -4rem;
  }
  100% {
    bottom: var(--distance, 10rem);
  }
}/*# sourceMappingURL=footer.css.map */