.my_work_container {
  width: 100%;
  padding-bottom: 20rem;
}
.my_work_container .filter_buttons_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 5rem 0;
}
.my_work_container .filter_buttons_container .filter_buttons {
  width: 100%;
  max-width: 60rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.my_work_container .filter_buttons_container .filter_buttons button {
  border: none;
  margin: 1rem;
  font-size: 1.4rem;
  padding: 1rem 1.5rem;
  border-radius: 50rem;
  font-weight: bold;
  background: transparent;
  box-shadow: 0.3rem 0.5rem 0.1rem black, 0px -0.2rem 1.8rem black;
  color: #FFFFFF;
  cursor: pointer;
}
.my_work_container .filter_buttons_container .filter_buttons button:hover {
  transform: scale(0.95);
}
@media only screen and (max-width: 600px) {
  .my_work_container {
    padding: 0 1rem;
    padding-bottom: 20rem;
  }
}
.my_work_container .all_projects {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
}
.my_work_container .all_projects .single_project {
  width: 100%;
  max-width: 25rem;
  position: relative;
  margin: 2rem 2rem;
  cursor: pointer;
  padding: 1rem;
  box-shadow: 0.2rem 0.2rem 1.8rem black;
  background: rgba(0, 0, 0, 0.225);
  -webkit-box-shadow: 0.2rem 0.2rem 1.8rem black;
  -moz-box-shadow: 0.2rem 0.2rem 1.8rem black;
}
@media only screen and (max-width: 600px) {
  .my_work_container .all_projects .single_project {
    margin: 2rem 0rem;
  }
}
.my_work_container .all_projects .single_project::after {
  content: "";
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  transition: 0.5s;
  z-index: 5;
  background: rgba(0, 0, 0, 0.625);
}
.my_work_container .all_projects .single_project span {
  position: absolute;
  font-weight: bold;
  z-index: 15;
  font-size: 1.3rem;
  padding: 1rem 1.5rem;
  border-radius: 50rem;
  border: 0.1rem solid white;
  background: rgba(0, 0, 0, 0.6);
  text-transform: uppercase;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  text-shadow: 0.1rem 0.1rem 0.5rem white;
  -webkit-animation: up_down 1s linear 0s infinite alternate !important;
          animation: up_down 1s linear 0s infinite alternate !important;
}
@-webkit-keyframes up_down {
  from {
    transform: translate(-50%, -50%) scale(1);
  }
  to {
    transform: translate(-50%, -50%) scale(1.1);
  }
}
@keyframes up_down {
  from {
    transform: translate(-50%, -50%) scale(1);
  }
  to {
    transform: translate(-50%, -50%) scale(1.1);
  }
}
.my_work_container .all_projects .single_project:hover::after {
  opacity: 0;
  z-index: -1;
}
.my_work_container .all_projects .single_project:hover span {
  opacity: 0;
  z-index: -1;
}
.my_work_container .all_projects .single_project div {
  width: 100%;
  max-height: 30rem;
  padding: 0 0.5rem;
  overflow-y: auto;
  overflow-x: hidden;
}
.my_work_container .all_projects .single_project div::-webkit-scrollbar {
  background: rgba(0, 0, 0, 0.3) !important;
  width: 0.5rem !important;
}
.my_work_container .all_projects .single_project div::-webkit-scrollbar-thumb {
  border-radius: 5rem;
  background: rgba(255, 255, 255, 0.401) !important;
}
.my_work_container .all_projects .single_project div::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1) !important;
}
.my_work_container .all_projects .single_project div img {
  width: 100%;
}
.my_work_container .all_projects .single_project h3 {
  text-align: center;
  position: relative;
  margin-top: 3rem;
  font-size: 1.6rem;
  z-index: 6;
  line-height: 3rem;
}
.my_work_container .all_projects .img_light_box {
  max-width: 35rem;
}

.perfect_position {
  top: 0% !important;
  left: 0% !important;
}

.video_player_container {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 1000000;
  transition: 1s;
  top: -150%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  background: rgba(0, 0, 0, 0.825);
  padding: 0 2rem;
}
.video_player_container iframe {
  position: relative;
  width: 100%;
  max-width: 100rem;
  max-height: 40rem;
  height: 100%;
}
@media only screen and (max-width: 900px) {
  .video_player_container iframe {
    max-width: 90rem;
    max-height: 30rem;
  }
}
@media only screen and (max-width: 1045px) {
  .video_player_container iframe {
    max-height: 35rem;
  }
}
@media only screen and (max-width: 790px) {
  .video_player_container iframe {
    max-height: 25rem;
  }
}
.video_player_container span {
  border: none;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 3%;
  left: 3%;
  background: transparent;
  font-size: 4rem;
  cursor: pointer;
  color: white;
}
.video_player_container span:hover {
  transform: translateX(0%) scale(1.1);
}

.img_viewer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
.img_viewer .image_viewer_container {
  max-width: 100rem;
  max-height: 90vh;
  overflow: auto;
}
.img_viewer .image_viewer_container img {
  width: 100%;
}

.active_button {
  background: #00bcd4 !important;
}/*# sourceMappingURL=portfolio.css.map */