.header_heading {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  min-height: 25rem;
  background: rgba(0, 0, 0, 0.3019607843);
}
@media only screen and (max-width: 600px) {
  .header_heading {
    min-height: 20rem;
    font-size: 3.5rem;
  }
}
.header_heading .wavi_title_container {
  margin: 4rem 0 !important;
}
@media only screen and (max-width: 900px) {
  .header_heading .wavi_title_container {
    margin: 2rem 0 !important;
  }
}
.header_heading .wavi_title_container h1 {
  font-size: 6rem;
  text-align: center;
}
@media only screen and (max-width: 900px) {
  .header_heading .wavi_title_container h1 {
    font-size: 4.5rem;
  }
}
@media only screen and (max-width: 350px) {
  .header_heading .wavi_title_container h1 {
    font-size: 3.5rem;
  }
}
.header_heading .wavi_title_container h1:nth-child(1) {
  -webkit-text-stroke: 0.1rem rgb(180, 180, 180) !important;
}
.header_heading .wavi_title_container h1:nth-child(2) {
  color: white;
}

.inner_container {
  padding-top: 10rem;
  width: 100%;
  padding-bottom: 2rem;
}

.sub_heading_container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 3rem 0;
  margin: 5rem 0;
  background: rgba(0, 0, 0, 0.3019607843);
}
@media only screen and (max-width: 600px) {
  .sub_heading_container {
    padding: 2rem 0;
    font-size: 2rem;
  }
}
@media only screen and (max-width: 900px) {
  .sub_heading_container .wavi_title_container {
    margin: 2rem 0 !important;
  }
}
.sub_heading_container .wavi_title_container h1 {
  font-size: 4rem;
  text-align: center;
}
@media only screen and (max-width: 900px) {
  .sub_heading_container .wavi_title_container h1 {
    font-size: 3rem;
  }
}
.sub_heading_container .wavi_title_container h1:nth-child(1) {
  -webkit-text-stroke: 0.1rem rgb(180, 180, 180) !important;
}
.sub_heading_container .wavi_title_container h1:nth-child(2) {
  color: white;
}

.skills_container {
  width: 100%;
  padding: 2rem 1rem;
  padding-bottom: 20rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.about_use_Container {
  position: relative;
  transition: 1.5s;
  width: 100%;
  height: auto;
  max-height: 3rem;
  max-width: 35rem;
  border-radius: 1rem;
  padding: 5rem 2rem;
  padding-top: 15rem;
  background: rgba(0, 0, 0, 0.35);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  margin: 0 auto;
  margin-top: 12rem;
}
.about_use_Container:hover .img_container img {
  opacity: 1 !important;
}
.about_use_Container:hover .img_container .profile_image_cover {
  opacity: 0 !important;
}
.about_use_Container:hover {
  max-width: 80rem;
  max-height: 150rem;
}
@media only screen and (max-width: 600px) {
  .about_use_Container:hover {
    max-height: 3000rem;
  }
}
.about_use_Container .profile_container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  position: absolute;
  z-index: 3;
  transform: translateY(-27rem);
  width: 100%;
}
.about_use_Container .profile_container .img_container {
  width: 100%;
  max-width: 20rem;
  position: relative;
}
.about_use_Container .profile_container .img_container .profile_image_cover {
  position: absolute;
  top: 50%;
  transition: 1s;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.421);
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.8rem;
  text-shadow: 1px 1px 12px #ffffff;
  font-weight: bold;
  border: 0.2rem solid white;
  color: white;
  border-radius: 20rem;
  width: 85%;
  height: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  text-align: center;
}
.about_use_Container .profile_container .img_container img {
  border: 1.5rem solid #1c1d1e;
  width: 100%;
  border-radius: 50%;
  width: 20rem;
  height: 20rem;
  opacity: 0.3;
  transition: 1s;
}
.about_use_Container .profile_container h1 {
  font-size: 2rem;
  margin-top: 1.5rem;
}
.about_use_Container .profile_container h3 {
  font-size: 1.1rem;
  font-weight: 100;
  max-width: 20rem;
  text-align: center;
  margin-top: 1rem;
  word-break: break-word;
}
.about_use_Container .content {
  overflow: hidden;
  transition: 10s;
  opacity: 0;
  margin: 2rem 0;
  margin-top: 3rem;
}
.about_use_Container .content p {
  text-align: center;
  white-space: pre-line;
}
.about_use_Container .btn_container {
  overflow: hidden;
  opacity: 0;
  transition: 4s;
  margin: 2rem 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  max-width: 40rem;
}
.about_use_Container .btn_container a, .about_use_Container .btn_container button {
  margin: 1rem;
}

.about_use_Container:hover .btn_container, .about_use_Container:hover .content {
  opacity: 1;
}/*# sourceMappingURL=about.css.map */