.service {
  width: 100%;
  max-width: 30rem;
  margin: 2rem 1rem;
  background: rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  padding: 1rem;
  padding-top: 3.5rem;
  box-shadow: 0.6rem 0.6rem 1rem 0.2rem black, -0.5rem -0.5rem 1rem 0.2rem rgba(0, 0, 0, 0.2784313725);
  border: none;
  border-radius: 4rem 0rem 4rem 0rem;
}
.service:hover {
  box-shadow: -0.6rem -0.6rem 1rem 0.2rem black, 0.5rem 0.5rem 1rem 0.2rem rgba(0, 0, 0, 0.2784313725);
  transform: scale(0.98);
}
.service p {
  max-width: 100%;
  text-align: center;
  margin: 1rem 0;
  word-break: break-word;
  font-size: 1.3rem;
}
.service h3 {
  font-size: 1.4rem;
  text-align: center;
  margin: 1rem 0;
  margin-top: 5rem;
}
.service .service_logo_container {
  border: 0.1rem solid #00bcd4;
  position: relative;
  width: 6rem;
  height: 6rem;
  transform: rotate(45deg);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.service .service_logo_container span {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  transform: rotate(-45deg);
  font-size: 3rem;
  color: #00bcd4;
}/*# sourceMappingURL=Service.css.map */