.loading_container {
  width: 100% !important;
  padding: 10rem 0 !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  flex-direction: row !important;
  flex-wrap: nowrap !important;
}
.loading_container .loading_outer {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  flex-direction: row !important;
  flex-wrap: nowrap !important;
  position: relative !important;
  width: 20rem !important;
  height: 20rem !important;
}
.loading_container .loading_outer .loading_inner {
  position: absolute !important;
  width: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  height: 100% !important;
  top: 0% !important;
  left: 0% !important;
  border-radius: 50% !important;
  border-top: 1rem solid #00bcd4 !important;
  box-shadow: 0.1rem 0.1rem 0.1rem #00bcd4, inset 0.1rem 0.1rem 0.5rem #00bcd4 !important;
  -webkit-animation: loading_rotation 2s linear 0s infinite;
          animation: loading_rotation 2s linear 0s infinite;
}
.loading_container .loading_outer h3 {
  color: #FFFFFF;
  font-size: 0.9em !important;
}

@-webkit-keyframes loading_rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loading_rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}/*# sourceMappingURL=loading.css.map */