.admin_panel_services {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.admin_panel_services div {
  margin: 2rem 1rem;
  border: 0.3rem solid rgba(255, 255, 255, 0.368627451);
  padding: 2rem 3rem;
  cursor: pointer;
  border-radius: 20rem;
}
.admin_panel_services div:hover {
  transform: scale(1.1);
}
.admin_panel_services div h3 {
  font-size: 3rem;
  color: white;
  text-align: center;
}
@media only screen and (max-width: 900px) {
  .admin_panel_services div h3 {
    font-size: 2.5rem;
  }
}
@media only screen and (max-width: 600px) {
  .admin_panel_services div h3 {
    font-size: 2rem;
  }
}
@media only screen and (max-width: 350px) {
  .admin_panel_services div h3 {
    font-size: 1.8rem;
  }
}

.admin_setting_container {
  width: 100%;
  position: fixed;
  transition: all 1s;
  border: none;
  box-shadow: 0px 0px 0px white, inset 0.1rem 0.1rem 3rem #2c3e50;
  top: -150%;
  left: 0;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.966);
  overflow-y: auto;
  z-index: 100;
  transition: 0.6s;
}

#close_admin_setting {
  position: -webkit-sticky;
  position: sticky;
  top: 2%;
  font-size: 2.5rem;
  color: white;
  cursor: pointer;
  left: 97%;
}
#close_admin_setting:hover {
  transform: scale(1.1);
}

.admin_profile_container {
  width: 100%;
}

.perfect_position {
  top: 0% !important;
  left: 0% !important;
}

#edit_update_button {
  position: -webkit-sticky;
  position: sticky;
  max-width: 12rem;
  top: 1%;
  font-size: 2.5rem;
  color: #00bcd4;
  cursor: pointer;
  left: 2%;
  background-color: rgba(255, 255, 255, 0.0588235294);
  border-radius: 50rem;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 0.5rem 2.5rem;
  border: 0.1rem solid #00bcd4;
}
#edit_update_button:hover {
  transform: scale(1.1);
}

.cond_disabled {
  cursor: pointer;
}

.cond_disabled::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.877);
  z-index: -4;
  transition: 0.5s;
  opacity: 0;
}

.cond_disabled:hover::after {
  opacity: 1;
  z-index: 4;
}

.cond_disabled:hover label {
  opacity: 1;
  z-index: 5;
}

.empty_heading_admin {
  width: 100% !important;
  text-align: center !important;
  font-size: 3rem !important;
  color: #00b2c8 !important;
  text-shadow: 0.2rem 0.3rem 1rem !important;
  margin: 3rem 0 !important;
}/*# sourceMappingURL=admin.css.map */