.AdminPanel_Projects {
  max-width: 105rem;
  width: 100%;
  margin: 0 auto;
  padding: 3rem 1rem;
}
.AdminPanel_Projects h1[target=heading] {
  text-align: center;
}
@media only screen and (max-width: 600px) {
  .AdminPanel_Projects h1[target=heading] {
    font-size: 2.5rem;
  }
}
.AdminPanel_Projects hr {
  max-width: 90% !important;
  margin: 3rem auto !important;
  color: white !important;
}

.project_filter_btn_container_outer {
  width: 100%;
  margin: 3rem 0;
  margin-top: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner span {
  margin: 1rem;
  position: relative;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner span button {
  padding: 1rem 1.5rem;
  border: none;
  border-radius: 5rem;
  background: #FFFFFF;
  color: black;
  font-weight: bold;
  position: relative;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner span button:hover {
  transform: scale(1.1);
}
.project_filter_btn_container_outer .project_filter_btn_container_inner span i {
  position: absolute;
  top: 50%;
  cursor: pointer;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  opacity: 0;
  z-index: 5;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner .filter_buttons {
  border-radius: 5rem;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner .filter_buttons::after {
  border: none !important;
  z-index: 5;
  content: "";
  border-radius: 5rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner .filter_buttons:hover i {
  opacity: 1;
  z-index: 10;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner .filter_buttons:hover button {
  background: transparent !important;
  z-index: -1;
}
.project_filter_btn_container_outer .project_filter_btn_container_inner .filter_buttons:hover {
  background: red;
}
.project_filter_btn_container_outer input {
  width: 100%;
  max-width: 60rem;
  color: #FFFFFF;
  font-size: 1.5rem;
  padding: 1.5rem 2rem;
  border: none;
  border-radius: 10rem;
  margin-bottom: 2.5rem;
  outline: none;
  box-shadow: 0.1rem 0.3rem 0.4rem #00bcd4, 0.1rem 0.1rem 10rem #00bcd4, inset 0rem 0rem 1.1rem #00bcd4;
  background: rgba(0, 239, 255, 0.3215686275);
}
.project_filter_btn_container_outer input::-moz-placeholder {
  color: #FFFFFF;
}
.project_filter_btn_container_outer input:-ms-input-placeholder {
  color: #FFFFFF;
}
.project_filter_btn_container_outer input::placeholder {
  color: #FFFFFF;
}

.project_container {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
.project_container .project_container_inner {
  margin: 3rem 0rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
}
.project_container .project_container_inner .single_project {
  width: 100%;
  margin: 1.5rem 2rem;
  overflow: hidden;
  position: relative;
  max-width: 30rem;
  padding: 2rem;
  background: #0e0e0e;
  border-radius: 1rem;
}
.project_container .project_container_inner .single_project:hover .edit_del_container {
  top: 0% !important;
}
.project_container .project_container_inner .single_project .project_img_container {
  width: 100%;
  max-height: 30rem;
  overflow-y: auto;
}
.project_container .project_container_inner .single_project .project_img_container img {
  width: 100%;
  height: 100%;
}
.project_container .project_container_inner .single_project h3 {
  color: White;
  text-align: center;
  margin-top: 1rem;
}
.project_container .project_container_inner .single_project .edit_del_container {
  position: absolute;
  background: rgba(0, 0, 0, 0.835);
  width: 100%;
  height: 100%;
  top: -100%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  border: 0.1rem solid rgba(255, 255, 255, 0.176);
  border-radius: 1rem;
}
.project_container .project_container_inner .single_project .edit_del_container i {
  font-size: 3rem;
  cursor: pointer;
  margin: 1rem;
  color: white;
}
.project_container .project_container_inner .single_project .edit_del_container i:hover {
  transform: scale(1.1);
}
.project_container .project_container_inner .single_project .edit_del_container .fa-edit:hover {
  color: green;
}
.project_container .project_container_inner .single_project .edit_del_container .fa-trash:hover {
  color: red;
}
.project_container .project_container_inner button {
  margin: 3rem 0;
}
.project_container h2 {
  text-align: center;
  margin-bottom: 5rem;
}

.portfolio_project_creater_updater {
  overflow-y: auto;
  position: fixed;
  padding: 2rem 1rem;
  width: 100%;
  height: 100%;
  max-width: 100% !important;
  max-height: 100% !important;
  overflow-x: hidden;
  top: 0px;
  left: -100%;
  z-index: 100;
  background: rgba(0, 0, 0, 0.938);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;
}
.portfolio_project_creater_updater .project_creation_portal_inner {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  max-width: 50rem;
  background-color: black;
}
.portfolio_project_creater_updater .project_creation_portal_inner .img_container {
  max-width: 25rem;
  overflow-y: auto;
  max-height: 30rem;
}
.portfolio_project_creater_updater .project_creation_portal_inner .img_container img {
  width: 100%;
}
.portfolio_project_creater_updater .project_creation_portal_inner h1 {
  margin: 3rem 0;
}
.portfolio_project_creater_updater .project_creation_portal_inner .common_input_style {
  width: 100%;
  padding: 1.5rem 2rem;
  font-size: 1.6rem;
  border-radius: 10rem;
  border: none;
  outline: none;
  color: white;
  margin: 2rem 0;
  box-shadow: 0.1rem 0.3rem 0.4rem #00bcd4, 0.1rem 0.1rem 10rem #00bcd4, inset 0rem 0rem 1.1rem #00bcd4;
  background: rgba(0, 239, 255, 0.3215686275);
}
.portfolio_project_creater_updater .project_creation_portal_inner .common_input_style::-moz-placeholder {
  color: #FFFFFF;
}
.portfolio_project_creater_updater .project_creation_portal_inner .common_input_style:-ms-input-placeholder {
  color: #FFFFFF;
}
.portfolio_project_creater_updater .project_creation_portal_inner .common_input_style::placeholder {
  color: #FFFFFF;
}
.portfolio_project_creater_updater .project_creation_portal_inner label {
  text-align: center;
  max-width: 25rem;
  cursor: pointer;
}
.portfolio_project_creater_updater .project_creation_portal_inner label:hover {
  transform: scale(1.1);
}
.portfolio_project_creater_updater .close_portal_icon {
  color: #00bcd4;
  font-size: 3.5rem;
  position: -webkit-sticky;
  position: sticky;
  top: 1%;
  cursor: pointer;
  left: 100%;
}
.portfolio_project_creater_updater .close_portal_icon:hover {
  transform: scale(0.95);
}

.perfect_position_projectportal {
  left: 0px !important;
}

.project_type_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}
.project_type_container span {
  width: 100%;
  display: block;
  text-align: center;
}
.project_type_container div {
  position: relative;
  margin: 1rem;
}
.project_type_container div i {
  display: none;
}
.project_type_container div button {
  color: white;
  font-weight: bold;
  box-shadow: 0.1rem 0.3rem 0.4rem #00bcd4, 0.1rem 0.1rem 10rem #00bcd4, inset 0rem 0rem 1.1rem #00bcd4;
  background: rgba(0, 239, 255, 0.3215686275);
}
.project_type_container div button:hover {
  transform: scale(1) !important;
}

.project_type_container .show_project_type {
  position: relative;
  margin: 1rem;
}
.project_type_container .show_project_type i {
  color: white !important;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  font-size: 2rem !important;
  position: absolute !important;
  top: 50% !important;
  left: 50% !important;
  width: 100% !important;
  height: 100% !important;
  border-radius: 50rem !important;
  background: rgba(0, 0, 0, 0.538) !important;
  transform: translate(-50%, -50%) !important;
}
.project_type_container .show_project_type button {
  color: white;
  font-weight: bold;
  box-shadow: 0.1rem 0.3rem 0.4rem #00bcd4, 0.1rem 0.1rem 10rem #00bcd4, inset 0rem 0rem 1.1rem #00bcd4;
  background: rgba(0, 239, 255, 0.3215686275);
}

.project_categories_container {
  width: 100%;
  padding: 2rem 1rem;
  max-width: 50rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.project_categories_container span {
  width: 100%;
  text-align: center;
  display: block;
  font-weight: bold;
  margin: 2rem 0;
}
.project_categories_container div {
  margin: 1rem;
}
.project_categories_container div input {
  margin-right: 0.5rem;
}
.project_categories_container div label {
  font-size: 1.6rem;
  text-transform: capitalize;
  font-weight: bold;
}

#creatin_portal_caller_btn {
  margin: 0 auto;
