.animate_btn {
  -webkit-animation: btn_animation 1s linear 0s infinite alternate;
          animation: btn_animation 1s linear 0s infinite alternate;
}
.animate_btn:hover {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
  cursor: pointer;
}

@-webkit-keyframes btn_animation {
  0% {
    transform: scale(1);
    box-shadow: 0px 0px 5px, inset 0px 0px 10px;
  }
  100% {
    box-shadow: 0px 0px 15px, inset 0px 0px 16px;
    transform: scale(1.04);
  }
}

@keyframes btn_animation {
  0% {
    transform: scale(1);
    box-shadow: 0px 0px 5px, inset 0px 0px 10px;
  }
  100% {
    box-shadow: 0px 0px 15px, inset 0px 0px 16px;
    transform: scale(1.04);
  }
}
.animate_btn_absolute {
  -webkit-animation: animate_btn_absolute 1s linear 0s infinite alternate;
          animation: animate_btn_absolute 1s linear 0s infinite alternate;
}
.animate_btn_absolute:hover {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
  cursor: pointer;
}

@-webkit-keyframes animate_btn_absolute {
  0% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow: 0px 0px 5px, inset 0px 0px 10px;
  }
  100% {
    box-shadow: 0px 0px 15px, inset 0px 0px 16px;
    transform: translate(-50%, -50%) scale(1.04);
  }
}

@keyframes animate_btn_absolute {
  0% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow: 0px 0px 5px, inset 0px 0px 10px;
  }
  100% {
    box-shadow: 0px 0px 15px, inset 0px 0px 16px;
    transform: translate(-50%, -50%) scale(1.04);
  }
}
