.progress_bar_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  margin: 2rem 1rem;
  max-width: 40rem;
}
.progress_bar_container h3 {
  width: 100%;
  text-align: left;
  margin: 1rem 0;
}
.progress_bar_container .progress_bar_outer {
  width: 100%;
  height: 2rem;
  position: relative;
  border-radius: 50rem;
  background: rgba(0, 0, 0, 0);
  box-shadow: 0.1rem 0.1rem 0.9rem #00bcd4, inset 0.1rem 0.1rem 0.8rem #00bcd4;
}
.progress_bar_container .progress_bar_outer span {
  position: absolute;
  top: -3rem;
  right: 1%;
}
.progress_bar_container .progress_bar_outer .progress_bar_inner {
  border-radius: 50rem;
  transition: 2s;
  height: 100%;
  background: #00bcd4;
}

.perfect_position_progress_bar {
  top: 2% !important;
}

.file_upload_progress_bar {
  width: 100% !important;
  position: fixed;
  background: rgba(0, 0, 0, 0.6588235294);
  min-height: 10rem;
  border-radius: 1rem;
  padding: 2rem;
  top: -150%;
  transition: 1.5s;
  left: 50%;
  transform: translateX(-50%);
  max-width: 40rem !important;
  z-index: 200;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  transition: 1s !important;
}
.file_upload_progress_bar .outer {
  transition: 1s !important;
