  justify-content: flex-start !important;
  align-items: center !important;
  position: relative;
  width: 100% !important;
  height: 1rem !important;
  border-radius: 5rem !important;
  padding: 0 !important;
  margin: 0 !important;
  background: #000000 !important;
  box-shadow: 0.1rem 0.3rem 0.6rem #00bcd4, inset 0.1rem 0.2rem 0.9rem #00bcd4 !important;
}
.file_upload_progress_bar .outer h5, .file_upload_progress_bar .outer h6 {
  padding: 0 !important;
  margin: 0 !important;
  position: absolute !important;
  font-size: 1.4rem;
  z-index: 20;
}
.file_upload_progress_bar .outer h5 {
  top: -320%;
  right: 1%;
}
.file_upload_progress_bar .outer h6 {
  left: 1%;
