#main_navbar, #main_navbar_rs {
  position: fixed;
  width: 20%;
  top: 0px;
  left: 0px;
  overflow-y: auto;
  height: 100vh;
  max-height: 100%;
}
#main_navbar::-webkit-scrollbar, #main_navbar_rs::-webkit-scrollbar {
  background: rgba(0, 0, 0, 0.3) !important;
  width: 0.5rem !important;
}
#main_navbar::-webkit-scrollbar-thumb, #main_navbar_rs::-webkit-scrollbar-thumb {
  border-radius: 5rem;
  background: rgba(255, 255, 255, 0.401) !important;
}
#main_navbar::-webkit-scrollbar-track, #main_navbar_rs::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1) !important;
}
#main_navbar .brand_container, #main_navbar_rs .brand_container {
  margin-top: 3.5rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
@media only screen and (max-width: 900px) {
  #main_navbar .brand_container, #main_navbar_rs .brand_container {
    margin-top: 1rem;
  }
}
#main_navbar .brand_container .brand_img_container, #main_navbar_rs .brand_container .brand_img_container {
  width: 100%;
  max-width: 15rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-bottom: 1rem;
}
#main_navbar .brand_container .brand_img_container img, #main_navbar_rs .brand_container .brand_img_container img {
  max-width: 12rem;
  max-height: 12rem;
  width: 100%;
  border-radius: 100%;
  border: 1rem solid rgba(0, 0, 0, 0.1803921569);
  margin: 0rem;
}
#main_navbar .brand_container h3, #main_navbar_rs .brand_container h3 {
  color: #FFFFFF;
  text-align: center;
  padding: 0 0.5rem;
}

#main_navbar, #main_navbar_rs {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  transition: 1s;
}
#main_navbar .nav_links_container, #main_navbar_rs .nav_links_container {
  margin-top: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
}
#main_navbar .nav_links_container a, #main_navbar_rs .nav_links_container a {
  width: 100%;
  margin: 0.5rem 0;
  transition: 1s;
  font-family: "Cinzel Decorative", cursive;
  font-weight: 700;
  font-size: 1.8rem;
  padding: 0.6rem 0;
  text-align: center;
  color: #FFFFFF;
}
#main_navbar .nav_links_container a:hover, #main_navbar_rs .nav_links_container a:hover {
  transform: scale(0.9);
  background: rgba(0, 0, 0, 0.626);
}

@media only screen and (max-width: 900px) {
  #main_navbar {
    display: none;
  }
}

#main_navbar_rs {
  display: none !important;
}
@media only screen and (max-width: 900px) {
  #main_navbar_rs {
    background: #1c1d1e;
    display: flex !important;
    width: 26rem;
    z-index: 20;
    left: -150%;
  }
}

.nav_social_icons {
  margin-top: auto;
  padding-bottom: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.nav_social_icons a {
  font-size: 2.2rem;
  margin: 0 0.5rem;
  padding: 0.5rem;
  box-shadow: -0.2rem -0.1rem 0.6rem #181818;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
.nav_social_icons a:hover {
  transform: scale(1.2);
}

.nav_toggle_btn {
  font-size: 2.5rem;
  color: white;
  position: absolute;
  top: 1%;
  right: 2%;
}

.active_page {
  color: #00bcd4 !important;
  background: rgba(0, 0, 0, 0.626);
}

#dropdown_navbar {
  width: 100%;
  padding: 1rem 2rem;
  z-index: 10;
  position: fixed;
  background: rgba(0, 0, 0, 0.7803921569);
  display: none;
}
#dropdown_navbar .logo_container {
  width: 100%;
}
#dropdown_navbar .logo_container h3 {
  font-size: 1.8rem;
  color: white;
  font-family: "Kalam", cursive;
}
#dropdown_navbar .logo_container h3 span {
  font-size: 1.8rem;
  color: #00bcd4;
}
#dropdown_navbar .logo_container #toggle_nav_btn {
  font-size: 2.5rem;
  color: white;
}
@media only screen and (max-width: 900px) {
  #dropdown_navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
  }
}/*# sourceMappingURL=nav.css.map */