.home_page {
  padding: 0 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("../../assets/bg3.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.home_page .inner_wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  height: 50rem;
}
.home_page .wavi_title_container {
  margin: 4rem 0 !important;
}
@media only screen and (max-width: 900px) {
  .home_page .wavi_title_container {
    margin: 2rem 0 !important;
  }
}
.home_page .wavi_title_container h1 {
  font-size: 6rem;
  text-align: center;
}
@media only screen and (max-width: 900px) {
  .home_page .wavi_title_container h1 {
    font-size: 4.5rem;
  }
}
@media only screen and (max-width: 600px) {
  .home_page .wavi_title_container h1 {
    font-size: 3rem;
  }
}
@media only screen and (max-width: 350px) {
  .home_page .wavi_title_container h1 {
    font-size: 2.5rem;
  }
}
.home_page .wavi_title_container h1:nth-child(1) {
  -webkit-text-stroke: 0.1rem rgb(180, 180, 180) !important;
}
.home_page .wavi_title_container h1:nth-child(2) {
  color: white;
}
.home_page h1, .home_page h3, .home_page a {
  text-align: center;
  margin: 1rem 0;
}

.home_typing_text {
  font-size: 2.5rem;
  color: #FFFFFF;
}
@media only screen and (max-width: 600px) {
  .home_typing_text {
    font-size: 2rem;
  }
}
@media only screen and (max-width: 350px) {
  .home_typing_text {
    font-size: 1.6rem;
  }
}
.home_typing_text span {
  font-size: 2.5rem;
  color: #00bcd4;
  text-shadow: 0.1rem 0.1rem 0.5rem;
}
@media only screen and (max-width: 600px) {
  .home_typing_text span {
    font-size: 2rem;
  }
}
@media only screen and (max-width: 350px) {
  .home_typing_text span {
    font-size: 1.6rem;
  }
}/*# sourceMappingURL=home.css.map */